<template>
  <div class="relative w-full" ref="dropdownContainer">
    <button
      :class="[
        'dropdown-button w-full',
        {
          'bg-zinc-200': shouldShowOptions && !hasOnlyImage,
          'hover:bg-zinc-200': !hasOnlyImage,
        },
        props.toggleButtonStyle,
      ]"
      @click="toggleOptions(!shouldShowOptions)"
    >
      <div class="dropdown-button__content">
        <v-icon :class="['!text-base/4', { 'mr-1': !hasOnlyIcon }]">
          {{ props.icon }}
        </v-icon>
        <img
          v-if="props.imageUrl"
          :src="props.imageUrl"
          :class="[
            'dropdown-image',
            {
              'dropdown-image__active': shouldShowOptions,
              'mr-1': !hasOnlyImage,
            },
          ]"
          alt="dropdown image"
        />
        <span v-if="props.text" class="dropdown-title">
          {{ dropdownTitle }}
        </span>
      </div>
      <v-icon v-if="!shouldHideToggleArrow" class="!text-base/4 ml-1 w-min">
        mdi-menu-down
      </v-icon>
    </button>
    <div
      v-if="shouldShowOptions"
      :class="[
        'absolute shadow-md bg-white z-50',
        {
          'left-0': [
            DropdownContentPosition.TopLeft,
            DropdownContentPosition.BottomLeft,
          ].includes(dropdownContentDirection),
          'right-0': [
            DropdownContentPosition.TopRight,
            DropdownContentPosition.BottomRight,
          ].includes(dropdownContentDirection),
        },
      ]"
      :style="dropdownStyle"
    >
      <ul class="w-max">
        <li
          v-for="dropdownItem in dropdownContent"
          :key="dropdownItem.text"
          :class="[
            'dropdown-item',
            {
              'hover:bg-zinc-100 cursor-pointer': !dropdownItem.disableHover,
              'bg-black-12 hover:bg-black-14':
                dropdownTitle === dropdownItem.text,
            },
          ]"
          @click="
            () => dropdownItem.value && onDropdownItemClick(dropdownItem.value)
          "
        >
          <v-icon v-if="dropdownItem.prependIcon" class="dropdown-item__icon">
            {{ dropdownItem.prependIcon }}
          </v-icon>
          <span>{{ dropdownItem.text }}</span>
          <v-icon v-if="dropdownItem.appendIcon" class="dropdown-item__icon">
            {{ dropdownItem.appendIcon }}
          </v-icon>
        </li>
      </ul>
    </div>
  </div>
</template>

<script lang="ts" setup>
import {
  computed,
  defineEmits,
  defineProps,
  onMounted,
  ref,
  withDefaults,
} from "vue";

import { DropdownContentPosition } from "../constants";

interface DropdownContent {
  text: string;
  appendIcon?: string;
  prependIcon?: string;
}

interface Props {
  dropdownContent: DropdownContent[];
  shouldHideToggleArrow?: boolean;
  icon?: string;
  imageUrl?: string;
  text?: string;
  toggleButtonStyle?: string;
}

const props = withDefaults(defineProps<Props>(), {
  shouldHideToggleArrow: false,
  icon: "",
  imageUrl: "",
  text: "",
  toggleButtonStyle: "",
});

const emits = defineEmits<{
  (e: "dropdown-item-click", value: string): void;
}>();

const dropdownTitle = ref<string>(props.text);
const shouldShowOptions = ref<boolean>(true);
const dropdownContainer = ref<HTMLDivElement | null>(null);

let dropdownContentWidth = 0;
let dropdownContentHeight = 0;
let toggleButtonHeight = 0;

const hasOnlyIcon = computed(
  () => props.icon && !props.text && !props.imageUrl
);

const hasOnlyImage = computed(
  () => props.imageUrl && !props.text && !props.icon
);

const dropdownContentDirection = computed(() => {
  if (shouldShowOptions.value) {
    const dropdownContainerRect =
      dropdownContainer.value?.getBoundingClientRect();
    const isBottomSpaceEnough =
      dropdownContentHeight + dropdownContainerRect?.top + toggleButtonHeight <
      window.innerHeight;
    const isRightSpaceEnough =
      dropdownContentWidth + dropdownContainerRect?.left < window.innerWidth;
    const isTopSpaceEnough =
      dropdownContainerRect?.top -
        (dropdownContentHeight + toggleButtonHeight) >
      0;

    switch (true) {
      case !isBottomSpaceEnough && !isRightSpaceEnough && isTopSpaceEnough:
        return DropdownContentPosition.BottomRight;

      case !isBottomSpaceEnough && isTopSpaceEnough:
        return DropdownContentPosition.BottomLeft;

      case !isRightSpaceEnough:
        return DropdownContentPosition.TopRight;

      default:
        return DropdownContentPosition.TopLeft;
    }
  }

  return "";
});

const dropdownStyle = computed(() => {
  if (
    dropdownContentDirection.value === DropdownContentPosition.BottomLeft ||
    dropdownContentDirection.value === DropdownContentPosition.BottomRight
  ) {
    return { bottom: toggleButtonHeight + "px" };
  }

  return {};
});

const toggleOptions = (toggleValue: boolean) =>
  (shouldShowOptions.value = toggleValue);

const onDropdownItemClick = (dropdownItem: string) => {
  dropdownTitle.value = dropdownItem;
  shouldShowOptions.value = false;
  emits("dropdown-item-click", dropdownItem);
};

document.addEventListener("click", (event: Event) => {
  if (!dropdownContainer.value?.contains(event?.target))
    shouldShowOptions.value = false;
});

onMounted(() => {
  dropdownContentWidth = dropdownContainer.value?.lastElementChild?.clientWidth;
  dropdownContentHeight =
    dropdownContainer.value?.lastElementChild?.clientHeight;
  toggleButtonHeight = dropdownContainer.value?.firstElementChild?.clientHeight;

  shouldShowOptions.value = false;
});
</script>

<style lang="scss" scoped>
.dropdown-button {
  width: 100%;
  font-size: 13px;
  letter-spacing: 0.52px;
  display: flex;
  align-items: center;

  &__content {
    display: flex;
    align-items: center;
  }
}

.dropdown-image {
  width: 28px;
  height: 28px;
  border: 1px solid rgb(224, 224, 224);
  object-fit: cover;
  object-position: 50% 0%;
  border-radius: 50%;

  &:hover,
  &__active {
    border-color: rgb(3, 169, 244);
    color: hsla(0, 0%, 0%, 0.078);
  }
}

.dropdown-title {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-transform: uppercase;
}

.dropdown-item {
  padding: 10px 11px 9px;
  font-size: 13px;
  font-weight: 400;
  text-align: left;

  &:first-child {
    margin-top: 4px;
  }

  &:last-child {
    margin-bottom: 4px;
  }

  &__icon {
    width: 29px;
    font-size: 18px;
    justify-content: left;
  }
}
</style>
