<template>
  <div
    class="w-screen h-screen flex justify-center items-center bg-black-6 z-50 absolute top-0 left-0"
  >
    <div class="add-user-container">
      <div
        class="add-user-container__title border border-b-gray-200 text-left flex items-center"
      >
        <h1 class="text-base/5 font-medium px-4">
          {{ NEW_CONTACT_TITLE }}
        </h1>
      </div>
      <div class="add-user-container__contents">
        <div class="drop-box" @click="onImageInputClick">
          <input type="file" class="hidden" ref="imageInputElement" />
          <span class="drop-box__text">{{ IMAGE_INPUT_BOX_TEXT }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { onMounted, ref } from "vue";

import { EmailRegexPattern } from "@/constants";

const rules = {
  required: (value: string | number) => !!value || "Required",
  email: (value: string) => {
    return EmailRegexPattern.test(value) || "Invalid e-mail";
  },
  length: (value: string) => value.length >= 5 || "Less than required limit",
};

const imageInputElement = ref<HTMLInputElement | null>(null);
const imageURL = ref<string>("");

const NEW_CONTACT_TITLE = "New Contact";
const IMAGE_INPUT_BOX_TEXT = "Click the area to select a photo from a folder";

const formFields = [
  {
    placeholder: "First Name",
    type: "text",
    rules: [rules.length, rules.required],
    value: "firstName",
  },
  {
    placeholder: "Last Name",
    type: "text",
    rules: [],
    value: "lastName",
  },
  {
    placeholder: "Company",
    type: "text",
    rules: [rules.length, rules.required],
    value: "company",
  },
  {
    placeholder: "Position",
    type: "text",
    rules: [rules.length, rules.required],
    value: "position",
  },
  {
    placeholder: "Assigned to",
    type: "text",
    rules: [rules.length, rules.required],
    value: "assignedTo",
  },
  {
    placeholder: "Phone",
    type: "number",
    prependIcon: "mdi-phone",
    rules: [rules.required],
    value: "phone",
  },
  {
    placeholder: "Email",
    type: "text",
    prependIcon: "mdi-email",
    rules: [rules.length, rules.required, rules.email],
    value: "email",
  },
  {
    placeholder: "Address",
    type: "text",
    prependIcon: "mdi-home",
    rules: [rules.length, rules.required],
    value: "address",
  },
];

const onImageInputClick = () => {
  imageInputElement.value?.click();
};

const onImageInputChange = () => {
  imageURL.value = URL.createObjectURL(imageInputElement.value?.files[0]);
};

onMounted(() => {
  imageInputElement.value?.addEventListener("change", onImageInputChange);
});
</script>

