<template>
  <div :class="['accordion-menu', props.containerStyle]">
    <button
      @click="toggleAccordionContent"
      :class="['toggle-button', props.toggleButtonStyle]"
    >
      <div class="toggle-button__title flex justify-start items-center">
        <v-icon v-if="props.prependIcon" class="prepend-icon">
          {{ props.prependIcon }}
        </v-icon>
        <span>{{ props.text }}</span>
      </div>
      <v-icon class="arrow-icon" v-if="!hideToggleArrow">
        {{ toggleArrow }}
      </v-icon>
    </button>
    <div
      class="slot-container"
      ref="accordionContentElement"
      :style="accordionContentStyle"
    >
      <slot name="content-slot" class="slot-content" />
    </div>
  </div>
</template>

<script lang="ts" setup>
import {
  computed,
  defineEmits,
  defineProps,
  onMounted,
  ref,
  withDefaults,
} from "vue";

interface Props {
  text: string;
  shouldShowAccordion: boolean;
  containerStyle?: string;
  hideToggleArrow?: boolean;
  prependIcon?: string;
  toggleButtonStyle?: string;
}

const props = withDefaults(defineProps<Props>(), {
  containerStyle: "",
  hideToggleArrow: false,
  prependIcon: "",
  toggleButtonStyle: "",
});

const emits = defineEmits<{
  (e: "toggle-button-click", value: string): void;
}>();

const accordionContentElement = ref<HTMLDivElement | null>(null);
let accordionContentHeight = ref<number | undefined>(
  accordionContentElement.value?.offsetHeight
);

let shouldCalculateHeight = true;

const toggleAccordionContent = () => {
  emits("toggle-button-click", props.text);
};

const toggleArrow = computed(() => {
  if (props.prependIcon) {
    return props.shouldShowAccordion ? "mdi-chevron-down" : "mdi-chevron-right";
  } else {
    return props.shouldShowAccordion ? "mdi-chevron-up" : "mdi-chevron-down";
  }
});

const accordionContentStyle = computed(() => {
  if (props.shouldShowAccordion || shouldCalculateHeight) {
    return {
      height: accordionContentHeight.value + "px",
    };
  }

  return { height: 0 };
});

onMounted(() => {
  accordionContentHeight.value = accordionContentElement.value?.offsetHeight;
  shouldCalculateHeight = false;
});
</script>

<style lang="scss" scoped>
.accordion-menu {
  width: 100% !important;
  height: max-content;

  .toggle-button {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 13px;
    line-height: 16px;

    &__title {
      width: 100%;
      font-size: inherit;
      font-weight: inherit;
      line-height: inherit;
    }

    .prepend-icon {
      min-width: 48px;
      font-size: 18px;
      color: rgba(0, 0, 0, 0.87);
    }

    .arrow-icon {
      font-size: 18px;
    }
  }

  .slot-container {
    transition: height 0.5s ease-in-out;
    overflow: hidden;
  }
}
</style>